 
.inform-item::before{
    content : "";
    position: absolute;
    width: 16px;
    height: 16px;
    border-radius: 50px;
    left: -9px;
    top: 0;
    background: #fff;
    border: 2px solid #0d6efd;
} 


.bg-pop-up{
  margin: 0;
  background-color: rgba(0, 0,0, .7);
  display: flex;
  -webkit-box-align: center;
  align-items: center;
  -webkit-box-pack: center;
  justify-content: center;
  height:100%;
  width: 100%;
  overflow: hidden;
  /* color: rgb(20, 30, 35); */
  position: fixed;
  top: 0px;
  left: 0px;
  z-index:90;  
}



.close{
  color: white;
  position: absolute;
  font-size: 25px;
  cursor: pointer;
}

.popupRight{
  /* border: 1px solid red; */
  justify-content: center;
  align-items: center;
}

.pop-up-content .cv-area{
  width: 100%;
  /* height: 550px; */
  height: 450px;
  margin-top: 20px;
  overflow-y: scroll;
  border: none;
 
}

/* width */
::-webkit-scrollbar {
  width: 8px;
}

/* Track */
::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px #0d6efd;
  
}
 
/* Handle */
::-webkit-scrollbar-thumb {
  background: #0d6efd;
  border-radius: 8px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #094eb4;
}
  
@media screen and ( max-width: 320px) {
  .pop-up-content .container{
    top: 0px;
    max-height: 100%;
    width: 90%;
    display: grid;
    /* grid-template-rows: auto 3fr auto; */
    justify-items: center;
    position: relative; 
    overflow: hidden;
    height: 450px;
  }


  .pop-up-content{
      max-height: 600px;

  }
 
  .pop-up-content .cv-area{
      max-height: 190px;
  }



}

  
