


/*--- ityped  ----*/

.ityped-cursor{
    animation: blink .8s infinite;
    color: #0d6efd;
    font-weight: bold;
}

@keyframes blink{
   50%{
        opacity: 1;
   }
   100%{
       opacity: 0;
   }
}
/*--- ityped  ----*/



#home .arrowDownIcon{
    font-size: 2.3rem;
    padding: 0;
    margin: 0;
}
#home .arrowIcon{
position: absolute;
bottom: 0;    
left: 0;
width: 100%;
text-align: center;
animation: downArrowAni 1s infinite;
}

@keyframes downArrowAni{
    100%{
          opacity: 0;
    }
}


@media screen and (min-width: 769px) {
    #home img{
        max-width: 60%;
    }

    #home   h1, h2, h3, span{
        font-size: 1.6rem;
    }
}


@media screen and  (max-width:768px){

    #home img{
        max-width: 70%;
    }

    #home   h1, h2, h3, span{
        font-size: 1.4rem;
    }
}


/* en son 601 px e kadar*/
@media screen and ( max-width: 601px){

    #home img{
        margin-top: 1rem;
        max-width: 45%;
    }

    #home  h2{
        margin-top: 1.3rem;
    }
    #home   h1, h2, h3, span{
        text-align: center;
        font-size: 1.5rem;
      
       
    } 
}




/*  en son 360px e kadar*/
@media screen and ( max-width: 360px){



    #home   h1, h2, h3, span{
        font-size: 1.2rem;
    }
    
}
